  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.image-render {
  margin-top: 1rem;
  width: 30rem;
  height: 15.93rem;
  cursor: pointer;
}

.back-button {
  width: -webkit-fit-content;
